import React from "react";

const personalInfoContent = [
  { meta: "Primeiro Nome", metaInfo: "Rui" },
  { meta: "Apelido", metaInfo: "Mota" },
  { meta: "Idade", metaInfo: "49 Anos" },
  { meta: "Nacionalidade", metaInfo: "Português" },
  { meta: "Freelance", metaInfo: "Disponível" },
  { meta: "Morada", metaInfo: "Lisboa" },
  { meta: "telemóvel", metaInfo: "+351 963 472 168" },
  { meta: "Email", metaInfo: "info@ruimota.pt" },
  { meta: "Skype", metaInfo: " rui.mota" },
  { meta: "linguagens", metaInfo: "Português, Inglês" },
];

const PersonalInfo = () => {
  return (
    <ul className="about-list list-unstyled open-sans-font">
      {personalInfoContent.map((val, i) => (
        <li key={i}>
          <span className="title">{val.meta}: </span>
          <span className="value d-block d-sm-inline-block d-lg-block d-xl-inline-block">
            {val.metaInfo}
          </span>
        </li>
      ))}
    </ul>
  );
};

export default PersonalInfo;
