import React from "react";

const experienceContent = [
  {
    year: "   2003",
    position: " Microsoft Gold Systems Engineer",
    compnayName: "",
    details: `   Planning, Implementing, Managing and Maintaining Microsoft Windows & Exchange `,
  },
  {
    year: "2004",
    position: " MCSE - Core Infrastructure",
    compnayName: "",
    details: ` Cloud Platform & Infrastructure (Security) `,
  },
  {
    year: "2004",
    position: "CCNA - Implementing and Administering Cisco Solutions",
    compnayName: "",
    details: ` Routing and Switching `,
  },
  {
    year: "2004",
    position: "HTI",
    compnayName: "",
    details: ` Hardening the Infrastructure  `,
  },
];

const Experience = () => {
  return (
    <ul>
      {experienceContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.position}
            <span className="place open-sans-font">{val.compnayName}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};




export default Experience;
