import React from "react";

const educationContent = [
  {
    year: "2005 - 2008",
    degree: "Microsoft SQL Server",
    institute: "",
    details: `  Querying Microsoft SQL Server with Transact-SQL, Implementing a Microsoft SQL Server Database, Developing Applications Using ADO.NET for Microsoft SQL Server, Administração de Bases de Dados SQL Server`,
  },
  {
    year: "2018",
    degree: "Graphic Design",
    institute: "",
    details: `Adobe Photoshop | Adobe Illustrator | Adobe InDesign`,
  },
  {
    year: "2019",
    degree: "UX & UI ",
    institute: "",
    details: `Adobe Dreamweaver | Front-End Development com HTML5 e CSS3 | Responsive Web Design & Development | JavaScript Foundations | Responsive WordPress`,
  },
  {
    year: "2020",
    degree: "Programação Web & Mobile ",
    institute: "",
    details: `Bases de dados MySQL | PHP (Hypertext Preprocessor)`,
  },

];


const Education = () => {
  return (
    <ul>
      {educationContent.map((val, i) => (
        <li key={i}>
          <div className="icon">
            <i className="fa fa-briefcase"></i>
          </div>
          <span className="time open-sans-font text-uppercase">{val.year}</span>
          <h5 className="poppins-font text-uppercase">
            {val.degree}
            <span className="place open-sans-font">{val.institute}</span>
          </h5>
          <p className="open-sans-font">{val.details}</p>
        </li>
      ))}
    </ul>
  );
};



export default Education;
